import React from 'react';
import {Modal,Switch } from 'antd';
import './modal.css';
import axios from 'axios';
import { DragDropContext, Droppable, Draggable } from 'react-beautiful-dnd';
// fake data generator
const getItems = count =>
  Array.from(count,  k => k).map((v, i) => ({
    id: `item-${i}`,
    layerName_CN: v.layerName_CN,
    layerName_EN:v.layerName_EN,
    bbox:v.bbox,
    crs:v.crs,
    type:v.type,
    layerType:v.layerType,
    select:v.select ? v.select : false,
    select1:v.select1 ? v.select1 : true,
  }));

const reorder = (list, startIndex, endIndex) => {
  const result = Array.from(list);
  const [removed] = result.splice(startIndex, 1);
  result.splice(endIndex, 0, removed);

  return result;
};
const grid = 8;

const getItemStyle = (isDragging, draggableStyle) => ({
  // some basic styles to make the items look a bit nicer
  userSelect: "none",
  padding: grid * 2,
  margin: `0 0 ${grid}px 0`,
  'borderBottom':'1px solid #d6d6d6',
  'marginLeft':'24px',

  // change background colour if dragging
  // background: isDragging ? "lightgreen" : "grey",

  // styles we need to apply on draggables
  ...draggableStyle
});
const getListStyle = isDraggingOver => ({
  // background: isDraggingOver ? "lightblue" : "lightgrey",
  // padding: grid,
  // 'border-bottom':'1px solid #d6d6d6',
  width: '100%',
  'overflowY':'auto',
  'maxHeight':'300px'
});
class CreateModal extends React.Component{
	constructor(props)
	{
		super(props)
        this.state = {
          items: [],
          disabled:false
        }
        this.onDragEnd = this.onDragEnd.bind(this);
        this.onChange = this.onChange.bind(this);
	}
	onChange(checked, key) {
        let b = this.props.uploadType
        let judge = b === 'draw' || b === 'route' || b === 'quickView';
        // debugger
        if(judge) {
          this.state.items.map(val => {
        
              val.select1 = !checked;
            
            
          })
        }
        
        let a = this.state.items.filter(v => {
            // debugger



            if (key == v.id) {
                v.select = checked;
                if(checked && judge){
                  // debugger
                  v.select1 = checked;
                }
                
                return v;
            };
        });
        // debugger
        let layers = this.props.map.getLayers();
        if(!checked)
        {
          let layer = layers.array_.filter(v => v.values_.name == a[0].layerName_CN);
          // debugger
          let reLayer = this.props.map.removeLayer(layer[0]);
          this.props.wmsParamsRender(reLayer);
        }else{
          // debugger
          // if( a[0].layerType === '2' ||  a[0].layerType === '3')
          // {
          //   this.props.showDataWFS({data:a})
          // }else{
          //   this.props.showData({data:a})
          // }
          a[0].layerType === '2' ? this.props.showDataWFS({data:a}) : this.props.showData({data:a});
          
        }
        
        // debugger
        
        this.props.handleModalHide();
  }
  
    onDragEnd (result) {
        // dropped outside the list
        if(!result.destination) {
          return;
        }

        const items = reorder(
          this.state.items,
          result.source.index,
          result.destination.index
        );
        this.props.showData({data:items})
        this.setState({
          items
        });
    }
    componentWillMount()
    {
        let data = new FormData();
        data.append('type',this.props.uploadType);
        axios({
          url:'http://192.168.8.254:8084/swgeoserver/geo/toList',
          method:'post',
          data:data,
        }).then(res => {
            // debugger
            if(this.props.uploadType === 'mapCompare')
            {
              this.setState({data:res.data})
            }else{
              // debugger
              const items = getItems(res.data);
              this.setState({items})
            }
            
        })
    }
    connect(data)
    {
        data[0].select = true;
        let items = getItems( [...this.state.items, ...data]);
    
        this.setState({items})
        // debugger
    }
    layerSwicth(e)
    {
      let value = e.target.textContent;
      let layers = this.props.map.getLayers();
      let layer = layers.array_.filter(layer => layer.values_.name === value);
      
      if(layer[0].getVisible() === true)
      {
        layer[0].setVisible(false);
        e.target.style['color'] = 'rgb(162, 157, 157)';
      }else{
        layer[0].setVisible(true);
        e.target.style['color'] = '#2780f8';
      }
      // debugger
    }
    render(){
        return (
            <Modal
                width={this.props.width||400}
                onCancel={() => this.props.handleModalHide()}
                visible={this.props.visible} 
                footer={null}
                className='layerModal'
            >
            	<div className='layerGroup'>
            		<div onClick={this.layerSwicth.bind(this)} className='layerIMG' style={{'marginLeft':'24px'}}>影像</div>
            		<div onClick={this.layerSwicth.bind(this)} className='layerVect'>矢量</div>
            		<div onClick={this.layerSwicth.bind(this)} className='layerPar'>透明矢量</div>
            	</div>
            	<div className='layerLine'></div>
            	<div className='layerTitle'>图层</div>
                <DragDropContext onDragEnd={this.onDragEnd}>
                    <Droppable droppableId="droppable">
                      {(provided, snapshot) => (
                        <div
                          {...provided.droppableProps}
                          ref={provided.innerRef}
                          style={getListStyle(snapshot.isDraggingOver)}
                        >
                          {this.state.items.map((item, index) => (
                            <Draggable key={item.id} draggableId={item.id} index={index}>
                              {(provided, snapshot) => (
                                <div
                                  ref={provided.innerRef}
                                  {...provided.draggableProps}
                                  {...provided.dragHandleProps}
                                  style={getItemStyle(
                                    snapshot.isDragging,
                                    provided.draggableProps.style
                                  )}
                                >
                                  {item.layerName_CN}
                                  <Switch disabled={!item.select1} defaultChecked={item.select} style={{float:'right',marginTop:'8px'}} size={'small'}  onChange={(checked) => this.onChange(checked, item.id)} />
                                </div>
                              )}
                            </Draggable>
                          ))}
                          {provided.placeholder}
                        </div>
                      )}
                    </Droppable>
                </DragDropContext>
            </Modal>
               
        )
    }
}
export default CreateModal;