	//添加绘制的要素
	addFeature(feature)
	{
		var geometry = feature.getGeometry().clone();
		console.log(geometry)
		debugger
      geometry.applyTransform(function(flatCoordinates, flatCoordinates2, stride) {
        for (var j = 0; j < flatCoordinates.length; j += stride) {
          var y = flatCoordinates[j];
          var x = flatCoordinates[j + 1];
          flatCoordinates[j] = x;
          flatCoordinates[j + 1] = y;
        }
      });
	  debugger
      // 设置feature对应的属性，这些属性是根据数据源的字段来设置的
	  var newFeature = new Feature();
	  Object.keys(feature.values_).forEach(key => {
		if(key !== 'geometry'){
			newFeature.set(key,feature.values_[key])
		}
	  })
			
      	newFeature.setGeometryName('geom');
      
		
		let wkt =  new WKT().writeGeometry(geometry);
		console.log(wkt)
		let type = wkt.split('(')[0]
		switch(type){
			case 'POLYGON':
				newFeature.setGeometry(new MultiPolygon([geometry.getCoordinates()]));
				break;
			case  'LINESTRING':
				newFeature.setGeometry(new MultiLineString([geometry.getCoordinates()]));
				break
			case 'POINT':
				newFeature.setGeometry(new Point([geometry.getCoordinates()]));
				break;
		}

		this.addWfs([newFeature]);
			// debugger
	}
