	showData(nextProps, connect)
	{
		if (connect) {
			this.refs.getSwordButton.connect(nextProps.data);
		}
		if (nextProps.data.length == 1) {
			for (var i = nextProps.data.length - 1; i >= 0; i--) {
				
				let wmsSource = new TileWMS({
			        url: 'http://192.168.8.254:8080/geoserver/wms',
			        params: {'LAYERS': nextProps.data[i].layerName_EN,'VERSION': '1.1.1',
			    	"exceptions": 'application/vnd.ogc.se_inimage',},
			        serverType: 'geoserver',
					crossOrigin: 'anonymous',
					wrapX: false,
				});
				// debugger
			    let wmsLayer = new TileLayer({
					source: wmsSource,
					name:nextProps.data[i].layerName_CN,
					nameEN:nextProps.data[i].layerName_EN
				});
				// debugger
			
			
				let obj = {source:wmsSource, layerName:nextProps.data[i].layerName_EN,layerType:nextProps.data[i].layerType};
				let wmsParams = [...this.state.wmsParams, obj];
				this.setState({wmsParams});
				let layers = [...this.state.layers, wmsLayer];
				this.setState({layers});
			
			    
				
			    map.addLayer(wmsLayer);
			    
			    let a = nextProps.data[i].bbox.split(',');
			    let m = a.map(v => {
			    	return Number(v);
			    })
			    let b = getCenter(m);
			    // debugger
			    map.getView().setCenter(b);
			}
			map.getLayers().forEach(val => {
				if(val.values_.name === '操作图层')
				{
					val.setZIndex(map.getLayers().array_.length);
				}
			})
			
		}else{
			for (var i = this.state.layers.length - 1; i >= 0; i--) {
				map.removeLayer(this.state.layers[i])
			}
			this.setState({layers:[]});
			this.setState({wmsParams:[]});
			for (var i = nextProps.data.length - 1; i >= 0; i--) {
				// debugger
				if (nextProps.data[i].select == true) {
					let wmsSource = new TileWMS({
				        url: 'http://192.168.8.254:8080/geoserver/wms',
				        params: {'LAYERS': nextProps.data[i].layerName_EN,'VERSION': '1.1.1',
				    	"exceptions": 'application/vnd.ogc.se_inimage',},
				        serverType: 'geoserver',
						crossOrigin: 'anonymous',
						wrapX: false,
				    });
				    let wmsLayer = new TileLayer({
						source: wmsSource,
						name:nextProps.data[i].layerName_CN,
						nameEN:nextProps.data[i].layerName_EN
					});
					let obj = {source:wmsSource, layerName:nextProps.data[i].layerName_EN,layerType:nextProps.data[i].layerType};
			    	let wmsParams = [...this.state.wmsParams, obj];
			    	this.setState({wmsParams});
				    map.addLayer(wmsLayer);
				    let layers = [...this.state.layers, wmsLayer];
				    this.setState({layers});
				}
			}
		}
	}
