	getSWDGMap (url, name){
		let layer = new TileLayer({
		        source: new TileImage({
		            projection: new getProjection("EPSG:3857"),
		            tileSize: [256, 256],
		            wrapX: false,
		            tileUrlFunction:this.getTileURL,
		            url:url,
		            attributions:'世景地图',

		        }),
		        name:name
		    });
	    return layer;
	}
