	createModify(feature)
	{
		let type = feature.getGeometry().getType();
		let lonlat = type==='MultiPolygon' ? feature.getGeometry().getCoordinates()[0] : feature.getGeometry().getCoordinates();
		let features = []
		lonlat[0].forEach(value => {
			let point = new Point(value)
			let feature = new Feature({
				geometry:point,
				name:'point'
			})
			feature.setStyle(
									
				new Style({
					image: new CircleStyle({
					  radius: 10,
					  fill: new Fill({
						color: 'blue'
					  })
					}),
					
				})
				
			)
			feature.on('mousein', function(){
				// 修改feature的样式为半径100像素的园，用蓝色填充
					this.setStyle(new Style({
						image: new CircleStyle({
							radius: 10,
							stroke: new Stroke({
								color: 'orange',
								width:2
							}),
							fill: new Fill({
								color:'blue'
							})
						})
					}));
				
			})
			features.push(feature)
		})
		
		map.on('pointermove',function(event){
			if(map.hasFeatureAtPixel(event.pixel)){
				map.forEachFeatureAtPixel(event.pixel, function(feature){
				// 为移动到的feature发送自定义的mousemove消息
					if(feature.get('name') === 'point')
					{
						feature.dispatchEvent({type: 'mousein'});
					}
				
				});
			}
			else
			{
				features.forEach(feature => {
					feature.setStyle(new Style({
						image: new CircleStyle({
							radius: 10,
							fill: new Fill({
							color: 'blue'
							})
						})
					}));
				})
				
			}	

		})
		


		let dealLayer = map.getLayers().array_.filter(layer => layer.values_.name === '操作图层')[0]
		dealLayer.getSource().clear();
		dealLayer.getSource().addFeatures(features);
	}
	deletePoint(event)
	{
		debugger
		event.preventDefault();
		if(map.hasFeatureAtPixel(event.pixel)){
			map.forEachFeatureAtPixel(event.pixel, function(feature){
			// 为移动到的feature发送自定义的mousemove消息
				if(feature.get('name') === 'point')
				{
					let coor = feature.getGeometry().getCoordinates();
					let dealLayer = map.getLayers().array_.filter(layer => layer.values_.name === '操作图层')[0];
					let selectF = selectInteraction.getFeatures().array_[0];
					let type = selectF.getGeometry().getType();
					let coorF = type==='MultiPolygon' ? selectF.getGeometry().getCoordinates()[0][0] : selectF.getGeometry().getCoordinates()[0];
					coorF.forEach((value,index) => {
						if(value[0] === coor[0] && value[1] === coor[1])
						{
							coorF.splice(index, 1)
						}
					})
					// debugger
					// coorF.splice(coorF.indexOf(coor), 2)
					type==='MultiPolygon'? selectF.getGeometry().setCoordinates([[coorF]]) : selectF.getGeometry().setCoordinates([coorF])
					dealLayer.getSource().removeFeature(feature);
				}
			
			});
		}

	}
