	getTileURL (tileCoord, pixelRatio, proj) {
	    let zoom = tileCoord[0];
	    let bx = tileCoord[1];
	    let by = -tileCoord[2] - 1;
	    let nGrade =  Math.ceil((zoom - 3) / 4);//Math.ceil求最小的整数但不小于本身.   
	    let nPreRow = 0, nPreCol = 0, nPreSize = 0;
	    let path='' ;
	    path +=zoom+"/";
	    for (let i = 0; i < nGrade; i++) {
	        let nSize = 1 << (4 * (nGrade - i));
	        let nRow = Math.floor((bx*1.0-nPreRow * nPreSize) / nSize);//Math.floor求最大的整数但不大于本身.
	        let nCol = Math.floor((by*1.0-nPreCol * nPreSize) / nSize);
	        path +=((nRow > 9) ? nRow : "0" + nRow);
	        path +=((nCol > 9) ? nCol : "0" + nCol);
	        path +="/";
	        nPreRow = nRow;
	        nPreCol = nCol;
	        nPreSize = nSize;
	    }
	    let id ='';
	    
	   //"<<"操作符是无符号左移动符，将1转化成16进制后向右移20位，不足的补0；
	   id += (((bx) & ((1 << 20) - 1))
	            + (((by) & ((1 << 20) - 1)) * Math.pow(2, 20))
	            + (((zoom) & ((1 << 8) - 1)) * Math.pow(2, 40)));
	    return this.urls[0].split('|')[0] +path+id+"."+ this.urls[0].split('|')[1];
	       
	}
