	searchData(wktCombine)
	{
		let filter,wmsSource,wmsLayer;
		
		filter = `WITHIN(GEOM,SRID = 4326; ${wktCombine})`
		if(this.props.uploadType === 'heatMap')
		{
			wmsSource = new ImageWMS({

				url: 'http://192.168.8.254:8080/geoserver/wms',
				params: {'VERSION': '1.1.1',
				"exceptions": 'application/vnd.ogc.se_inimage','CQL_FILTER':filter,sld:'http://192.168.8.254:8080/geoserver/styles/person_point_heat.sld'},
				serverType: 'geoserver',
				crossOrigin: 'anonymous',
				wrapX: false,
			})
			wmsLayer = new ImageLayer({
				source: wmsSource,
					
				nameEN:'workspaceTest:accMV03'
			});
			debugger
		}else{
			wmsSource = new TileWMS({
				url: 'http://192.168.8.254:8080/geoserver/wms',
				params: {'LAYERS': 'workspaceTest:accMV03','VERSION': '1.1.1',
				"exceptions": 'application/vnd.ogc.se_inimage','CQL_FILTER':filter},
				serverType: 'geoserver',
				crossOrigin: 'anonymous',
				wrapX: false,
			});
			
			wmsLayer = new TileLayer({
				source: wmsSource,
				
				nameEN:'workspaceTest:accMV03'
			});
		}
		
		
		
		map.addLayer(wmsLayer);
		map.getLayers().forEach(val => {
			if(val.values_.name === '操作图层')
			{
				val.setZIndex(map.getLayers().length);
			}
		})
		wktCombine = '';
	}
	analisy(wktCombine)
	{
		this.setState({'loading':true})
		let filter
		
		filter = `WITHIN(GEOM,SRID = 4326; ${wktCombine})`;
		let url = 'http://192.168.8.254:8080/geoserver/workspaceTest/wfs?service=wfs&version=1.1.0&request=GetFeature&typeNames=workspaceTest:accMV03&srs=EPSG%3A4326&SRS=EPSG%3A4326&maxFeatures=100000000&outputformat=json';
		url += `&CQL_FILTER=${filter}`
		axios({
			url:url,
			method:'post',
			
		}).then(res => {
			
			let arr = res.data.features;
			this.setState({'loading':false})
			if(!arr){
				message.info('无数据！')
				return
			}
			this.setState({'modalVisible':true})
			// debugger
			let A0_20=[], A21_40=[], A41_60=[], A61_80=[], A81_100=[];
			arr.forEach(val => {
				let num = Number(val.properties.CLOUDCOVER);
				
				if(num >= 0 && num <= 20)
				{
					A0_20.push(num);
				}else if(num >= 21 && num <= 40)
				{
					A21_40.push(num);
				}else if(num >= 41 && num <= 60)
				{
					A41_60.push(num);
				}else if(num >= 61 && num <= 80)
				{
					A61_80.push(num);
				}else{
					A81_100.push(num);
				};
				// debugger

			})
			
			let option = {
				tooltip: {
					trigger: 'item',
					formatter: "{a} <br/>{b}: {c} ({d}%)"
				},
				title : {
					text: `数据总量${res.data.features.length}`,
					// subtext: '由世景应用技术部绘制',
					x: 'center',
					align: 'right'
				},
				legend: {
					orient: 'vertical',
					x: 'left',
					data:['0-20%','21%-40%','41%-60%','61%-80%','81%-100%']
				},
				series: [
					{
						name:'云量覆盖率',

						type:'pie',
						radius: ['50%', '70%'],
						avoidLabelOverlap: false,
						label: {
							normal: {
								show: false,
								position: 'center'
							},
							emphasis: {
								show: true,
								textStyle: {
									fontSize: '30',
									fontWeight: 'bold'
								}
							}
						},
						labelLine: {
							normal: {
								show: false
							}
						},
						data:[
							{value:A0_20.length, name:'0-20%'},
							{value:A21_40.length, name:'21%-40%'},
							{value:A41_60.length, name:'41%-60%'},
							{value:A61_80.length, name:'61%-80%'},
							{value:A81_100.length, name:'81%-100%'}
						]
					}
				]
			};
			let chart = echarts.init(document.getElementById('dataCharts'))
			
			chart.setOption(option);
		})
		wktCombine = '';
	}
