	//点击路径分析按钮开始分析路径
	startCountRoute()
	{
		let layer = map.getLayers().array_.filter(layer => layer.values_.name === '路径分析');
		if(layer.length !== 0)
		{
			message.info('请先清除上次的分析结果！');
			return;
		}
		if(routePoint.length === 2)
		{
			var params = {

				LAYERS: 'workspaceTest:atobyunnan',
		
				FORMAT: 'image/png',
		
				CRS:'EPSG:4326',
				VERSION: '1.1.1'
		
			};
			if(routePoint[0].get('pointType') !== '设置起点'){
				routePoint.reverse();
			}
			var viewparams = [

				'x1:' + routePoint[0].getGeometry().getCoordinates()[0],'y1:' + routePoint[0].getGeometry().getCoordinates()[1],
		
				'x2:' + routePoint[1].getGeometry().getCoordinates()[0], 'y2:'+ routePoint[1].getGeometry().getCoordinates()[1]
		
			];
			
			params.viewparams = viewparams.join(';');

		
			let result = new ImageLayer({
		
				source: new ImageWMS({
		
						url:'http://192.168.8.254:8080/geoserver/workspaceTest/wms',
		
						params: params
		
				}),
				name:'路径分析'
		
			});
		
			map.addLayer(result);
			axios({
				url:'http://192.168.8.254:8084/swgeoserver/pg/findRoute',
				method:'post',
				params:{x1:routePoint[0].getGeometry().getCoordinates()[0],y1:routePoint[0].getGeometry().getCoordinates()[1],
				x2:routePoint[1].getGeometry().getCoordinates()[0],y2:routePoint[1].getGeometry().getCoordinates()[1],
				dbName:'yunnan'}
			}).then(res => {
				if(res.data.length === 0){
					message.info('当前所选位置之间无任何可行路线！')
					let layer = map.getLayers().array_.filter(layer => layer.values_.name === '路径分析');
					map.removeLayer(layer[0]);
					return;
				}
				let html = '';
				res.data.forEach(val => {
					
					
						html += `<div data-isView=false style='cursor:pointer' wkt='${val.geom}' class='routeDiv'>${val.name}</div>`

				});
				// html += '</table>'
				// document.getElementById('routeInfor').innerHTML = html;
				// document.getElementById('routeInfor').style.translateX = '0px';
				$('#routeInfor').html(html);
				$('.routeDiv').bind('click', this.drawRoute)
				$('#routeInfor').animate({'left':'0px'});
			})
			
		}else if(routePoint.length === 0){
			message.info('请添加起点和终点！')
		}else{
			routePoint.forEach(val => {
				if(val.get('pointType') === '设置起点')
				{
					message.info('请添加终点！');
				}else{
					message.info('请添加终点');
				};
			})
		}
	}
