import React from 'react';
import {Modal, Collapse, Radio, message} from 'antd';

import { Vector as VectorSource} from 'ol/source.js';
import Draw, {createRegularPolygon, createBox} from 'ol/interaction/Draw.js';
import Polygon from 'ol/geom/Polygon.js';
import {Modify, Snap} from 'ol/interaction.js';

import WKT from 'ol/format/WKT';
const Panel = Collapse.Panel;
const RadioGroup = Radio.Group;
let draw, source, modify ,snap;

export default class ModalDraw extends React.Component
{
	constructor(props)
	{
		super(props)
		this.state = {
            'FeaturesValue':'',
            'FreehandValue':'',
            'ShapesValue':''
		}
    }
    componentWillUnmount()
    {
        draw = null;
        source = null;
        modify = null;
        snap = null;
    }
	onChange = (stateString, e) => {
        let evt = e || window.event;
        // console.log('radio checked', evt.target.value);
        let hasItem = this.props.getItem();
        if(hasItem.length === 0){
            message.info('请先打开一层图层！')
            return;
        }
        // debugger
        let obj = {};
        obj[stateString] = evt.target.value;
        this.setState(obj);
        if(!source && !modify)
        {
            source = new VectorSource({wrapX: false});
            let that = this;
            source.on('change', function(e){
                that.props.map.removeInteraction(draw);
                that.setState({FeaturesValue:'',FreehandValue:'',ShapesValue:''})

            })
            let layer = this.props.map.getLayers().array_.filter(layer => layer.values_.name === '操作图层');
            layer[0].setSource(source);
            // let modify = new Modify({source:source});
            // this.props.map.addInteraction(modify);
            // snap = new Snap({source: source});
            // this.props.map.addInteraction(snap);
        }
        
        // debugger

        
        // if(draw)
        // {
        //     this.props.map.removeInteraction(draw);
        // }
        this.addInteraction(evt.target.value, stateString);
        
        
        // debugger
    }
    
    addInteraction(type, stateString)
    {
        if (type !== 'None') {
            switch(stateString)
            {
                case 'FeaturesValue':
                    draw = new Draw({
                        source: source,
                        type: type,
                        // style:styles
                    });
                break;
                case 'FreehandValue':
                    draw = new Draw({
                        source: source,
                        type: type,
                        freehand: true,
                        finishCondition:this.drawFinish
                    });
                break;
                case 'ShapesValue':
                    let geometryFunction = createRegularPolygon(99);
                    if (type === 'Square') {
                        type = 'Circle';
                        geometryFunction = createRegularPolygon(4);
                    } else if (type === 'Box') {
                        type = 'Circle';
                    geometryFunction = createBox();
                    } else if (type === 'Star') {
                        type = 'Circle';
                    geometryFunction = function(coordinates, geometry) {
                            let center = coordinates[0];
                            let last = coordinates[1];
                            let dx = center[0] - last[0];
                            let dy = center[1] - last[1];
                            let radius = Math.sqrt(dx * dx + dy * dy);
                            let rotation = Math.atan2(dy, dx);
                            let newCoordinates = [];
                            let numPoints = 12;
                            for (let i = 0; i < numPoints; ++i) {
                            let angle = rotation + i * 2 * Math.PI / numPoints;
                            let fraction = i % 2 === 0 ? 1 : 0.5;
                            let offsetX = radius * fraction * Math.cos(angle);
                            let offsetY = radius * fraction * Math.sin(angle);
                            newCoordinates.push([center[0] + offsetX, center[1] + offsetY]);
                            }
                            newCoordinates.push(newCoordinates[0].slice());
                            if (!geometry) {
                            geometry = new Polygon([newCoordinates]);
                            } else {
                            geometry.setCoordinates([newCoordinates]);
                            }
                            
                            return geometry;
                        };
                    }
                    draw = new Draw({
                    source: source,
                    type: type,
                    geometryFunction: geometryFunction
                    });
                break;
                
            }   
            let that = this;
            draw.on('drawend', function(e) {
                // 绘制结束时暂存绘制的feature
                that.props.setFeature(e.feature);
                that.props.map.removeInteraction(draw);
            });
            this.props.map.addInteraction(draw);
            // debugger
        }
       
        // debugger
        this.props.handleModalHide()

    }
	render()
	{
        // let a = this.props
        // debugger
		return(
			<Modal
				width={this.props.width||400}
	            visible={this.props.visible} 
	            footer={null}
	            className='drawModal'
	            onCancel={() => this.props.handleModalHide()}
			>
				<Collapse accordion>
                    <Panel header="绘制要素" key="1">
                        <RadioGroup onChange={() => this.onChange('FeaturesValue')} value={this.state.FeaturesValue}>
                            <Radio value={'Point'}>点</Radio>
                            <Radio value={'LineString'}>线</Radio>
                            <Radio value={'Polygon'}>面</Radio>
                            {/* <Radio value={'Circle'}>Circle</Radio> */}
                            <Radio value={'None'}>无</Radio>
                        </RadioGroup>
                    </Panel>
                    <Panel header="手绘" key="2">
                        <RadioGroup onChange={() => this.onChange('FreehandValue')} value={this.state.FreehandValue}>
                            
                            <Radio value={'LineString'}>线</Radio>
                            <Radio value={'Polygon'}>面</Radio>
                            {/* <Radio value={'Circle'}>Circle</Radio> */}
                            <Radio value={'None'}>无</Radio>
                        </RadioGroup>
                    </Panel>
                    <Panel header="绘制形状" key="3">
                        <RadioGroup onChange={() => this.onChange('ShapesValue')} value={this.state.ShapesValue}>
                            
                            <Radio value={'Square'}>正方形</Radio>
                            <Radio value={'Box'}>长方形</Radio>
                            <Radio value={'Circle'}>圆形</Radio>
                            <Radio value={'Star'}>星形</Radio>
                            <Radio value={'None'}>无</Radio>
                        </RadioGroup>
                    </Panel>
                </Collapse>
			</Modal>
		)
	}
}