import React from 'react'
import 'ol/ol.css';
import { Modal, Icon } from 'antd';
import './mapCompare.css'
import TileWMS from 'ol/source/TileWMS.js';
import TileLayer from 'ol/layer/Tile.js';
// import { bbox } from 'ol/loadingstrategy';
import $ from 'jquery';
import { func } from 'prop-types';
// import { debug } from 'util';

const url = 'http://192.168.8.254:8080/geoserver/wms';
let eventCom, eventCom1;
export default class MapCompare extends React.Component
{
    constructor(props)
    {
        super(props)
        this.state = {
            item:{},
            topLayer:{},
            bottomLayer:{}
        }
    }
    startCompare()
    {
        let top = this.state.topLayer;
        let bottom = this.state.bottomLayer;
        if(Object.keys(top).length === 0 || Object.keys(bottom).length === 0)
        {
            return;
        }
        let topKey = Object.keys(top)[0];
        let bottomKey = Object.keys(bottom)[0];
        let arr = [bottom[bottomKey], top[topKey]];
        // debugger
        arr.forEach((value,index) => {
            let wmsSource = new TileWMS({
                url: 'http://192.168.8.254:8080/geoserver/wms',
                params: {'LAYERS': value['layerName_EN'],'VERSION': '1.1.1',
                "exceptions": 'application/vnd.ogc.se_inimage'},
                serverType: 'geoserver',
                crossOrigin: 'anonymous',
                wrapX: false,
            });
            
            let wmsLayer = new TileLayer({
                source: wmsSource,
                
                nameEN:value['layerName_EN']
            });
            this.props.map.addLayer(wmsLayer);
            if(index === 1){
                wmsLayer.setProperties({'position':'top'});
                // debugger
                eventCom1 = wmsLayer.on('precompose',  function(event){
                    var ctx = event.context;
                    var width = ctx.canvas.width * (swipe.value / 100);
            
                    ctx.save();
                    ctx.beginPath();
                    ctx.rect(width, 0, ctx.canvas.width - width, ctx.canvas.height);
                    ctx.clip();
                });
            
                eventCom = wmsLayer.on('postcompose', function(event) {
                    var ctx = event.context;
                    ctx.restore();
                });
            }
        })
        
        let swipe = document.getElementById('swipe');
        let layer = this.props.map.getLayers().array_.filter(layer => layer.values_.name === '透明矢量')[0];
        layer.setZIndex(this.props.map.getLayers().array_.length)
        
        this.props.map.render();
        let that = this
        swipe.addEventListener('input', function() {
            that.props.map.render();
        }, false);
        // document.getElementById('swipe').style.display = 'block';
        $('#swipe').show();
    }
    componentWillUnmount()
    {
        $('#swipe').hide();
    }
    setElement(item){
        this.setState({item:item[0]})
        // debugger
    }
    getImageUrl(layerName, map){
        
        
        let item = this.state.item;
        Object.keys(item).forEach(key => {
            item[key].forEach((value, index) => {
                if(value['layerName_EN'] === layerName)
                {
                    item[key].splice(index, 1);
                    // let src = `${url}?SERVICE=WMS&VERSION=1.1.1&REQUEST=GetMap&FORMAT=${encodeURIComponent('image/png')}&TRANSPARENT=true&LAYERS=${encodeURIComponent(layerName)}&exceptions=${encodeURIComponent('application/vnd.ogc.se_inimage')}&SRS=${encodeURIComponent('EPSG:4326')}&STYLES=&WIDTH=164&HEIGHT=200&BBOX=${encodeURIComponent(value['bbox'])}`;
                    let obj = {};
                    obj[key] = value;
                    obj['index'] = index;
                    if(map === 'top')
                    {
                        // $('.comTop>img').attr('src',src)
                        this.setState({topLayer:obj}, this.startCompare)
                    }else{
                        // $('.comBottom>img').attr('src',src)
                        this.setState({bottomLayer:obj}, this.startCompare)
                    }
                    
                    // debugger
                }
            })
        })
        this.setState({item});
        
        
        // debugger
    }
    dragStart(ev)
    {
        let layerName = ev.target.getAttribute('layerName');
        let layerGroup = ev.target.getAttribute('layergroupname');
        ev.dataTransfer.setData("layerName", layerName);
        ev.dataTransfer.setData("layerGroup", layerGroup);
        // debugger
    }
    dragStartIMG(ev)
    {
        let position = ev.target.getAttribute('mapPosition');
        ev.dataTransfer.setData("position", position);
    }
    dragEnd(ev)
    {
        ev.preventDefault();
        let position = ev.dataTransfer.getData("position");
        // debugger
        if(position)
        {
            let map1 = ev.currentTarget.getAttribute('mapPosition');
            // map1 !== position ? null : this.setState({topLayer:this.state.bottomLayer, bottomLayer:this.state.topLayer})
            if( map1 !== position)
            {
                let bottomLayer = this.state.bottomLayer;
                let topLayer = this.state.topLayer;
                let bottomEN = bottomLayer[Object.keys(bottomLayer)[0]].layerName_EN;
                let topEN = topLayer[Object.keys(topLayer)[0]].layerName_EN;
                let bottomL = this.props.map.getLayers().array_.filter(layer => layer.values_.nameEN === bottomEN)[0];
                let topL = this.props.map.getLayers().array_.filter(layer => layer.values_.nameEN === topEN)[0];
                let bottomSource = bottomL.getSource();
                let topSource = topL.getSource();
                bottomL.setSource(topSource);
                topL.setSource(bottomSource)
                // debugger
                this.setState({topLayer:bottomLayer, bottomLayer:topLayer})
            }
            
        }else{
            $('.layer').attr('draggable', false).css('cursor', 'auto');
            let layerGroup = ev.dataTransfer.getData("layerGroup");
            $('.layer[layergroupname='+layerGroup+']').attr('draggable', true).css('cursor', 'pointer');
            let map = ev.target.getAttribute('mapPosition');
            let layerName = ev.dataTransfer.getData("layerName");
            let ifLeft = $(ev.target).hasClass('left');
            if(!ifLeft){
                let item = map === 'top' ? this.state.topLayer : this.state.bottomLayer;
                let keys = Object.keys(item);
                
                
                let data = this.state.item[keys[0]];
                let index = keys[1];
                data.splice(item[index], 0, item[keys[0]]); 
                let layerName = map + 'Layer';
                this.state[layerName] = {};
                this.setState({item:this.state.item,layerName:this.state[layerName]})
            }
            // debugger
            // debugger
            this.getImageUrl(layerName, map);
        }
        
       
    }
    draging(ev)
    {
        ev.preventDefault(); 
    }
    cancelLayer(ev)
    {
        let layerPosition = $(ev.currentTarget).attr('mapPosition');
        debugger
        let item = layerPosition === 'top' ? this.state.topLayer : this.state.bottomLayer;
        let keys = Object.keys(item);
        if(keys.length === 0) return;
        let layer = this.props.map.getLayers().array_.filter(layer => layer.values_.nameEN === item[keys[0]].layerName_EN)[0];
        $('#swipe').hide();
        this.props.map.removeLayer(layer)
        let data = this.state.item[keys[0]];
        let index = keys[1];
        data.splice(item[index], 0, item[keys[0]]); 
        let layerName = layerPosition + 'Layer';
        this.state[layerName] = {};
        this.setState({item:this.state.item,layerName:this.state[layerName]})
        if(Object.keys(this.state.topLayer).length === 0 && Object.keys(this.state.bottomLayer).length === 0){
            $('.layer').attr('draggable', true).css('cursor', 'pointer');
        }
        // debugger
    }
    render()
    {
        
        return(
            <div>
            <Modal
				width={this.props.width||600}
	            visible={this.props.visible} 
	            footer={null}
	            className='compareModal'
                onCancel={() => this.props.handleModalHide()}

			>
                <div className='compareName'>上层地图</div>
                <div className='compareName compareNameC'>地图列表</div>
                <div className='compareName'>底层地图</div>
                <div 
                className='left comTop' 
                onDrop={this.dragEnd.bind(this)}
                onDragOver={this.draging.bind(this)}
                mapPosition={'top'}
                onClick={this.cancelLayer.bind(this)}
                >
                {
                    Object.keys(this.state.topLayer).map(key => {
                        if(key === 'index')
                        {
                            return;
                        }
                        // debugger
                        let item = this.state.topLayer[key];
                        // debugger
                        let src = `${url}?SERVICE=WMS&VERSION=1.1.1&REQUEST=GetMap&FORMAT=${encodeURIComponent('image/png')}&TRANSPARENT=true&LAYERS=${encodeURIComponent(item.layerName_EN)}&exceptions=${encodeURIComponent('application/vnd.ogc.se_inimage')}&SRS=${encodeURIComponent('EPSG:4326')}&STYLES=&WIDTH=164&HEIGHT=200&BBOX=${encodeURIComponent(item['bbox'])}`;
                        return(
                            <img onDragStart={this.dragStartIMG.bind(this)} mapPosition={'top'} src={src}></img>
                        )
                    })
                }
                    
                    {/* 上层 */}
                </div>
                <div className='left comLayer'>
                {
                    Object.keys(this.state.item).map(key => {
                        return(
                            <div className={'compareList'}
                                
                            >
                                <span>{key}</span>
                                {
                                    this.state.item[key].map(value => {
                                        // return(Object.keys(value).map(key1 => {
                                            // debugger
                                            // if(key1 === 'layerName_CN')
                                            // {
                                                // debugger
                                                return(
                                                
                                                    <div 
                                                        draggable="true"
                                                        onDragStart={this.dragStart.bind(this)}
                                                        layerName={value['layerName_EN']}
                                                        layerGroupName={key}
                                                        className='layer'
                                                    >
                                                        { value['layerName_CN']}
                                                    </div>
                                                )
                                            // }
                                            
                                        // }))
                                        
                                    })
                                }
                                
                            </div>
                        )
                    })
                }
                </div>
                <div 
                className='left comBottom' 
                onDrop={this.dragEnd.bind(this)}
                onDragOver={this.draging.bind(this)}
                mapPosition={'bottom'}
                onClick={this.cancelLayer.bind(this)}
                >
                {
                    Object.keys(this.state.bottomLayer).map(key => {
                        
                        if(key === 'index')
                        {
                            return;
                        }
                        let item = this.state.bottomLayer[key];
                        // debugger
                        let src = `${url}?SERVICE=WMS&VERSION=1.1.1&REQUEST=GetMap&FORMAT=${encodeURIComponent('image/png')}&TRANSPARENT=true&LAYERS=${encodeURIComponent(item.layerName_EN)}&exceptions=${encodeURIComponent('application/vnd.ogc.se_inimage')}&SRS=${encodeURIComponent('EPSG:4326')}&STYLES=&WIDTH=164&HEIGHT=200&BBOX=${encodeURIComponent(item['bbox'])}`;
                        return(
                            <img onDragStart={this.dragStartIMG.bind(this)} mapPosition={'bottom'} src={src}></img>
                        )
                    })
                }
                    {/* 下层 */}
                </div>
                {/* <Icon onClick={this.startCompare.bind(this)} className="startCompare" type="play-circle" theme="twoTone" /> */}
            </Modal>
                <input id="swipe" type="range" />
               
            </div>
        )
    }
}
