import React from 'react';
import './modal1.css';
import { Modal, message } from 'antd';
import axios from 'axios'
import imgURL from './upload/yangshi_daolu.png';
import imgURL1 from './upload/yangshi_dianzhuang.png';
import imgURL2 from './upload/yangshi_mianzhuang.png';
import imgURL3 from './upload/yangshi_quanyaosu.png';
import imgURL4 from './upload/yangshi_xianzhuang.png';

class ModalUpload extends React.Component
{
	constructor(props)
	{
		super(props)
		this.uploadFile = this.uploadFile.bind(this)
	}
	uploadFile()
	{
		this.props.loading(true)
		const file = this.props
		let data = new FormData();
    	data.append('file',file.file);
		axios({
	      url:'http://192.168.8.254:8084/swgeoserver/geo/upload',
	      method:'post',
	      data:data,
	      headers: {"Content-Type": "multipart/form-data"}
	    }).then(res => {
	      // this.setState({'mapdata': res})
          // debugger
	      	this.props.showData(res, true);
	      	this.props.clearFile();
			this.props.handleModalHide()
			this.props.loading(false)
	    }).catch(error => {
			this.props.clearFile();
			this.props.handleModalHide()
			this.props.loading(false)
			message.info('上传失败，请检查数据格式和网络状态后重新上传！')
		})
		// debugger
		
	}
	cancelUpload()
	{
		this.props.clearFile();
		this.props.handleModalHide()
	}
	render()
	{
		return(
			<Modal
                width={this.props.width||'35rem'}
                onCancel={this.cancelUpload.bind(this)}
                onOk={this.uploadFile}
                visible={this.props.visible} 
                cancelText={'放弃'}
                closable={false}
                title={'上传数据'}
                okText={'发布'}
                wrapClassName={'uploadModal'}
            >
            	<div className='uploadName'>
            		<span>数据名称：</span>
            		<span>{this.props.fileName}</span>
            	</div>
            	<div className='uploadStyle'>
            		<span className='styleLeft'></span>
            		<div className='styleContainer' style={{marginLeft:0}}>
            			<figure style={{'display':'inline-block'}}>
            				<img src={imgURL} />
            				<p style={{textAlign:'center',color:'#fff'}}>道路样式</p>
            			</figure>
            		</div>
            		<div className='styleContainer'>
            			<figure style={{'display':'inline-block'}}>
            				<img src={imgURL1} />
            				<p style={{textAlign:'center',color:'#fff'}}>道路样式</p>
            			</figure>
            		</div>
            		<div className='styleContainer'>
            			<figure style={{'display':'inline-block'}}>
            				<img src={imgURL2} />
            				<p style={{textAlign:'center',color:'#fff'}}>道路样式</p>
            			</figure>
            		</div>
            		<div className='styleContainer'>
            			<figure style={{'display':'inline-block'}}>
            				<img src={imgURL3} />
            				<p style={{textAlign:'center',color:'#fff'}}>道路样式</p>
            			</figure>
            		</div>
            		<div className='styleContainer'>
            			<figure style={{'display':'inline-block'}}>
            				<img src={imgURL4} />
            				<p style={{textAlign:'center',color:'#fff'}}>道路样式</p>
            			</figure>
            		</div>
            		<span className='styleRight'></span>
            	</div>
            </Modal>
		)
	}
}

export default ModalUpload;