import {Menu, Layout, Icon, Dropdown} from 'antd';
import {Link} from 'react-router-dom';
import React from 'react';
import './layout.css';


const { Header} = Layout;
class ShowHeader extends React.Component
{
	

	render()
	{
		// const props = this.props;
		// debugger
		const menu = (
			<Menu>
				<Menu.Item>
					<Link to={{ pathname: '/upload/' + 'boundary',}} className="home-link">
						海量矢量
					</Link>
				</Menu.Item>
				<Menu.Item>
					<Link to={{ pathname: '/upload/' + 'mapCompare',}} className="home-link">
						影像对比
					</Link>
				</Menu.Item>
				<Menu.Item>
					<Link to={{ pathname: '/upload/' + 'heatMap',}} className="home-link">
						热力图
					</Link>
				</Menu.Item>
				<Menu.Item>
					<Link to={{ pathname: '/upload/' + 'quickView',}} className="home-link">
						快视图
					</Link>
				</Menu.Item>
			</Menu>
		);
		return(
			<Header style={{ position: 'fixed', zIndex: 1, width: '100%' }}>
		      <div className="logo" />
		      <Menu
		        theme="dark"
		        mode="horizontal"
		        defaultSelectedKeys={['1']}
		        selectedKeys={[this.props.select]}
		        style={{ lineHeight: '64px', 'textAlign':'left'}}
		      >
		        <Menu.Item key="1">
		        	<Link to={{ pathname: '/',}} className="home-link">
              			首页
          			</Link>

		        </Menu.Item>
				<Menu.Item key="3">
		        	<Link to={{ pathname: '/upload/' + 'shp', }} className="home-link">
              			矢量数据
          			</Link>
		        </Menu.Item>
		        <Menu.Item key="2">
		        	<Link to={{ pathname: '/upload/' + 'tif'}} activeclassname='active' className="home-link">
              			栅格影像
          			</Link>
		        </Menu.Item>

		        
		        <Menu.Item key="4">
		        	<Link to={{ pathname: '/upload/' + 'draw',}} className="home-link">
              			矢量编辑
          			</Link>
		        </Menu.Item>
		        <Menu.Item key="5">
		        	<Link to={{ pathname: '/upload/'+'route', }} className="home-link">
              			路径分析
          			</Link>
		        </Menu.Item>
		        <Menu.Item key="6">
		        	{/* <Link to={{ pathname: '/upload',}} className="home-link"> */}
									<Dropdown overlay={menu}>
										<a className="ant-dropdown-link" href="#">
											更多<Icon type="down" />
										</a>
									</Dropdown>
          			{/* </Link> */}
		        </Menu.Item>
		      </Menu>
		    </Header>
		)
	}
}

export default ShowHeader;